<template>
    <view class="withdraw_page">
        <!-- tabbar -->
        <cu-custom><block slot="content">充值</block></cu-custom>
        <view class="things_width_full flex justify-start align-center use_money">充值到钱包</view>
        <view class="flex justify-center align-start input_value_box">
            <view class="input_value_title">充值金额</view>
            <view class="flex justify-start align-center input_box">
                <view class="input_label">￥</view>
                <input class="withdraw_money" type="digit" placeholder="输入金额" :value="pay_money" @input="inputPayMoneyFun" />
            </view>
        </view>
        <!-- 支付按钮 -->
        <view class="flex justify-center align-center btn_box">
            <button class="cu-btn recharge_btn" :disabled="buttonType" @tap="balanceRechargeFun">充值</button>
        </view>
        <!-- loading弹框组件 -->
		<popLinkDialog></popLinkDialog>
		<popLoading></popLoading>
		<popDialog></popDialog>
		<tipModal></tipModal>
		<!-- 登录 -->
		<login></login>
    </view>
</template>

<script>
import { balanceRecharge_api } from '@/api/index.js';
	import { appData } from '@/utils/config.js';
	import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
	import storage from '@/utils/storage.js';
    import { identity_format } from '@/utils/filters.js';
    import checkForm from '@/utils/checkFormData.js';
    import payFun from '@/utils/pay';
	let that = null;
    export default {
        data() {
            return {
                appBaseData:null,//全局配置信息
                userInfo: {},
                pay_money: '',// 充值的余额
                // 全局按钮状态
                buttonType: false,
            }
        },
        methods: {
            ...mapMutations([
                'modal',
                'base'
            ]),
            /**输入金额
             * @description: 
             * @param {*} e
             * @return {*}
             */
            inputPayMoneyFun(e) {
                that.pay_money = e.detail.value;
            },
            /**充值到余额
             * @description: 
             * @param {*} e
             * @return {*}
             */
            balanceRechargeFun(e) {
                if (!that.pay_money || Number(that.pay_money) === 0) {
                    that.modal({name:'popTipModal',value:{type:true,tipTitle:'提示',tipContent:'请输入充值金额'}});
                    return false;
                };
                that.buttonType = true;
                that.modal({name:'popLoading',value:true});
                let params = {
                    pay_money: that.pay_money,
                    channel_type: 1
                };
                balanceRecharge_api(params,that).then(res=>{
                    payFun.wxPay(res.payment,that).then(res=>{
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                        that.base({name:'refreshPage',value:true});
                        that.modal({name:'popTipModal',value:{type:true,tipTitle:'恭喜',tipContent:'充值成功'}});
                    }).catch(err=>{
                        console.log(err);
                        that.modal({name:'popLoading',value:false});
                        that.buttonType = false;
                    })
                }).catch(err=>{
                    console.log(err);
                    that.buttonType = false;
                    that.modal({name:'popLoading',value:false});
                })
            }
        },
        onLoad: function(options) {
            that = this;
        }
    }
</script>
<style lang="scss" scoped>
    @import "./index.scss";
</style>